.alert {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 300px;
    height: 60px;
    background-color: rgb(224, 79, 103);
    color:whitesmoke;
    border-radius: 5px;
    font-size: 18px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    
  }
  
  .enter {
    opacity: 0;
    right: 0;
    transform: translateX(100%);
  }
  
  .enterActive {
    opacity: 1;
    transform: translateX(0);
    transition: all 250ms linear;
  }
  
  .exit {
    opacity: 1;
    transform: translateX(0);
  }
  
  .exitActive {
    opacity: 0;
    transform: translateX(100%);
    transition: all 250ms linear;
  }